import { Command } from '@ali/4ever-cangjie';
import toolbar from './toolbar';
import menubar from './menubar';
import renderNode from './renderNode';
import { DEPRECATED_ACTIONS, actions } from './actions';
import onAction from './handlers/onAction';
import onKeyDown from './handlers/onKeyDown';
import insertHr from './commands/insertHr';
import quickInsert from './quickInsertConfig';
import { queries } from './queries';
import * as utils from './utils';
import { BiPlugin, BiPluginConfig } from '../../types';
import createRenderHoverToolbar from './createRenderHoverToolbar';
import { moFactory } from '@ali/4ever-plugin-hr';

const commands: Record<string, Command> = {
  insertHr,
};

function createHrPlugin(config: BiPluginConfig): BiPlugin {
  const hrConfig = config.hr || {};
  return {
    ...moFactory({}),
    toolbar: toolbar(hrConfig),
    menubar: menubar(),
    renderNode,
    actions,
    onAction,
    queries,
    onKeyDown,
    commands,
    quickInsert,
    models: {
      klass: 'block',
      type: 'hr',
    },
    renderHoverToolbar: createRenderHoverToolbar(hrConfig),
  };
}

createHrPlugin.actions = DEPRECATED_ACTIONS;
createHrPlugin.commands = commands;
createHrPlugin.utils = utils;

export default createHrPlugin;
